import React, { useRef } from "react";
import name from "./assets/name2.png";
import logo from "./assets/logo2.png";
import About from "./components/About";
import Services from "./components/Services";
// import { Container } from './styles';

function App() {
  function scrollToRef(ref) {
    window.scrollTo({
      top: ref.current.offsetTop,
      behavior: "smooth"
    });
  }
  const homeRef = useRef(null);
  const aboutRef = useRef(null);
  const servicesRef = useRef(null);

  function executeScroll(refName) {
    scrollToRef(refName);
  }

  return (
    <div className="container">
      <div className="header" ref={homeRef}>
        <div className="content">
          <div className="left">
            <img src={logo} className="logo" alt="icon" />
            <img className="name" src={name} alt="name" />
          </div>
          <nav>
            <ul>
              <li>
                <button onClick={() => executeScroll(homeRef)}>Início</button>
              </li>
              <li>
                <button onClick={() => executeScroll(aboutRef)}>Sobre</button>
              </li>
              <li>
                <button onClick={() => executeScroll(servicesRef)}>
                  Portifólio
                </button>
              </li>
            </ul>
          </nav>
        </div>
      </div>
      <About ref={aboutRef} />
      <Services ref={servicesRef} />
      <footer>
        <h4>
          Robótica Tucuju Enterprise - 2019 | contato@roboticatucuju.com.br
        </h4>
      </footer>
    </div>
  );
}

export default App;
